import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Grupo_formulario } from '../../../modelo/Grupo_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'grupo_formulario-dados-details',   templateUrl: 'grupo_formulario_dadosDetails.component.html' }) export class Grupo_formularioDadosDetailsComponent implements OnInit {    grupo_formulario: Grupo_formulario;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.grupo_formulario.Grupo_formulario";                 let id = +params['id'];         this.grupo_formulario = new Grupo_formulario();         this.grupo_formulario.cd_grupo_formulario = id;         o.objetoJson = this.grupo_formulario;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a grupo_formulario. '; }      this.grupo_formulario = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


